                <div class="text_box">
                    <p>
                        Ï†ÄÏû•Îêú ÌîºÎìúÍ∞Ä ÏóÜÏäµÎãàÎã§!<br>ÎßàÏùåÏóê ÎìúÏãúÎäî ÌîºÎìú Ï†ÄÏû•Î∂ÄÌÑ∞ Î∂ÄÌÉÅÎìúÎ¶ΩÎãàÎã§üôè
